import {Observable} from 'rxjs';
import {ApiResult} from './../../../models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {SellerUrlShop} from 'src/app/core/utils/URL/seller-url/seller-shop/seller-url-shop';
import {ShopInforResponse} from 'src/app/core/models/seller/shop/response/ShopInforResponse';
import {ShopInforUpdateResquest} from 'src/app/core/models/seller/shop/request/ShopInforUpdateRequest';
import { ImageUploadPreviewResponse } from 'src/app/core/models/seller/shop/response/ImageUploadPreviewResponse';

@Injectable({
	providedIn: 'root'
})
export class SellerShopService {
	constructor(private http: HttpClient, private sellerUrlShop: SellerUrlShop) {}
	
	getShopInfor(): Observable<ApiResult<ShopInforResponse>> {
		return this.http.get<ApiResult<ShopInforResponse>>(this.sellerUrlShop.sellerGetShopInfor());
	}
	putShopInfor(request: ShopInforUpdateResquest): Observable<ApiResult<boolean>> {
		return this.http.put<ApiResult<boolean>>(this.sellerUrlShop.sellerPutShopInfor(), request);
	}
  postAvatarImagePreview(newProductImage: FormData):Observable<ApiResult<ImageUploadPreviewResponse>> {
		return this.http.post<ApiResult<ImageUploadPreviewResponse>>(
			this.sellerUrlShop.sellerPostAvatarImagePreviewAPI(),
			newProductImage
		);
	}
}
