import {formatDate} from '@angular/common';
import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {ShopInforUpdateResquest} from 'src/app/core/models/seller/shop/request/ShopInforUpdateRequest';
import {ShopInforResponse} from 'src/app/core/models/seller/shop/response/ShopInforResponse';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {SellerShopService} from 'src/app/core/services/seller/seller-shop/seller-shop.service';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-shop-edit',
	templateUrl: './shop-edit.component.html',
	styleUrls: ['./shop-edit.component.less']
})
export class ShopEditComponent implements OnInit {
	isLoading = false; //return true: something is loading and false if something is completed
	submitted = false; //return true if submitted
	returnUrl = '';
	shopInforResponse: ShopInforResponse; //the personal infor after taking from store
	shopInforForm: FormGroup; //form Group of information fields
	host: string = environment.apiUrl; //host of server
	/**is modified any personal infor in formGroup, return true: any fields changed, return false: fields is similar with init data */
	modified = false;
	thumbshandlePreview: string; //url string of avatar image after uploading to server
	isThumbshandlePreview: boolean; //is url avatar image string null? return false if null => can not submit and true if not null
	isModalVisible: boolean;
	constructor(
		private formBuilder: FormBuilder,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private sellerShopService: SellerShopService,
		private notificationService: NotificationService,
		private spinnerService: SpinnerService
	) {
		this.isModalVisible = false;
		this.thumbshandlePreview = '';
		this.shopInforResponse = {} as ShopInforResponse;
		this.isThumbshandlePreview = false;
		//init form
		this.shopInforForm = this.formBuilder.group({
			name: [{value: ''}, [Validators.required, Validators.minLength(3), Validators.pattern]]
		});
	}
	ngOnInit(): void {
		this.getShopInfor(); //get personal infor from store
		this.initFormDataFromStore(); //attach data from store into formGroup
		this.checkFormValueChange(); //check any fields changed
	}
	getShopInfor() {
		this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.sellerShopService
			.getShopInfor()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						console.log(result);
						if (result.isSuccessed) {
							if (result.resultObj) {
								this.shopInforResponse = result.resultObj;
								this.thumbshandlePreview = result.resultObj.imagePath;
								console.log(result.resultObj.imagePath);
								console.log(this.thumbshandlePreview);
								this.isLoading = false;
								//this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
					}
				},
				(error) => {
					this.isLoading = false;
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	initFormDataFromStore() {
		this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		setTimeout(() => {
			this.shopInforForm.setValue({
				name: this.shopInforResponse.shopName != null ? this.shopInforResponse.shopName : ''
			});

			console.log(this.thumbshandlePreview);
			this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
		}, 5000);
	}
	checkFormValueChange() {
		this.shopInforForm.valueChanges.subscribe((result) => {
			console.log('valueChanges');
			console.log(this.shopInforResponse.shopName);
			console.log(result.name);
			console.log(this.shopInforResponse.imagePath);
			console.log(this.thumbshandlePreview);

			if (
				result.name === (this.shopInforResponse.shopName != null ? this.shopInforResponse.shopName : '') &&
				this.thumbshandlePreview ===
					(this.shopInforResponse.imagePath != null ? this.shopInforResponse.imagePath : '')
			) {
				this.modified = false;
			} else {
				this.modified = true;
			}
			console.log(this.modified);
		});
	}
	confirmUpdate() {
		this.submitForm();
	}
	submitForm(): void {
		try {
			this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			const val = this.shopInforForm.value;
			const profileUpdate: ShopInforUpdateResquest = {
				shopName: val.name,
				imagePath: this.thumbshandlePreview
			};
			this.sellerShopService
				.putShopInfor(profileUpdate)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(res) => {
						if (res) {
							if (res.isSuccessed) {
								if (res.message) {
									this.notificationService.showSuccessNotification(res.message);
								}
							} else {
								if (res.message) {
									this.notificationService.showErrorNotification(res.message);
								}
							}
						}
						this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
					}
				);
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}

	removeImageFile() {
		this.thumbshandlePreview = '';
		// if (this.shopInforResponse.imagePath) {
		// 	if (
		// 		this.thumbshandlePreview ===
		// 		(this.shopInforResponse.imagePath != null ? this.shopInforResponse.imagePath : '')
		// 	) {
		// 		//this.modified = false;
		// 	} else {
		// 		//this.modified = true;
		// 	}
		// 	//this.isThumbshandlePreview = false;
		// } else {
		// }
		this.modified = false;
		this.checkFormValueChange();
	}
	cancelDeleteImage(): void {
		this.isModalVisible = false;
		return;
	}

	confirmDeleteImage(): void {
		this.removeImageFile();
		this.checkFormValueChange();
		this.notificationService.showSuccessNotification('Đã Xóa Thành Công');
	}
	beforeUpload = (file: File): boolean => {
		const formData: FormData = new FormData();
		formData.append('productImage', file, file.name);
		this.sellerShopService
			.postAvatarImagePreview(formData)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);

					if (result.resultObj != null) {
						if (result.resultObj.imagePath != null) {
							this.thumbshandlePreview = result.resultObj.imagePath;
							this.isThumbshandlePreview = true;
							this.modified = true;
							//this.checkFormValueChange();
						}
						console.log(this.thumbshandlePreview);
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
		return false;
	};
}
