import {Injectable} from '@angular/core';
import {SellerUrlBase} from './../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlShop extends SellerUrlBase {
	sellerGetShopInfor(): string {
		return this.sellerUrl + '/seller/shop';
	}
	sellerPutShopInfor(): string {
		return this.sellerUrl +  '/seller/shop';
	}
	sellerPostAvatarImagePreviewAPI(): string {
		return this.sellerUrl + '/seller/shop/image-preview';
	}
}
